<template>
  <label
    class="my-radio"
    :class="{
      'my-radio-selected': selected,
      'my-radio-disabled': disabled || myDisabled,
      [`my-radio-${size}-border`]: border,
      [`my-radio-${size}-button`]: button,
      'my-radio-selected-button': selected && button,
    }"
  >
    <input
      class="my-radio-input"
      type="radio"
      @click="onClick"
      :disabled="disabled || myDisabled"
    />
    <span
      class="my-radio-icon"
      :class="{
        'my-radio-icon-selected': selected,
        'my-radio-icon-disabled': disabled || myDisabled,
        'my-radio-icon-button': button
      }"
    >
    </span>
    <span class="my-radio-label">
      <slot></slot>
    </span>
  </label>
</template>

<script>
// å·¥å·å½æ°ï¼ç¨äºå¤æ­ä¼ å¥çå¼æ¯å¦ç¬¦åæ¡ä»¶
import { oneOf } from '../../utils/assist'

import Emitter from '../../mixins/emitter'

export default {
  name: 'myRadio',
  mixins: [Emitter],
  data() {
    return {
      selected: false, // æ¯å¦è¢«éä¸­
      myDisabled: false, // åé¨çç¦ç¨å±æ§ï¼äº¤ç±ç¶çº§æ§å¶
      button: false, // æé®æ ·å¼
    }
  },
  props: {
    value: {
      type: [String, Number, Boolean],
      default: '',
    },
    label: {
      type: [String, Number, Boolean],
      default: '',
    },
    // ç¦ç¨ç¶æ
    disabled: {
      type: Boolean,
      default: false,
    },
    // æ¯å¦ç»å¶è¾¹æ¡
    border: {
      type: Boolean,
      default: false,
    },
    // å°ºå¯¸
    size: {
      validator(value) {
        return oneOf(value, ['large', 'medium', 'small', 'mini'])
      },
      type: String,
      default: 'medium',
    },
  },
  watch: {
    // åå§åå¤æ­æ¯å¦å·²è¢«éä¸­
    value: {
      handler(newVal) {
        this.selected = this.value && this.value === this.label
      },
      immediate: true,
    },
  },
  mounted() {
    // éç¥myRadioGroupç»ä»¶è°ç¨on-radio-addæ¹æ³ï¼åæ°ä¸ºå½åradioå®ä¾
    this.dispatch('myRadioGroup', 'on-radio-add', this)
  },
  beforeDestroy() {
    // ç§»é¤æ¶ï¼è°ç¨myRadioGroupç»ä»¶çon-radio-removeæ¹æ³
    this.dispatch('myRadioGroup', 'on-radio-remove', this)
  },
  methods: {
    onClick() {
      if (!this.selected) {
        this.$emit("change", true);
      }
      this.selected = true
      this.$emit('input', this.label)
      this.dispatch('myRadioGroup', 'on-radio-select', this)
    },
  },
}
</script>
